n  }\n\n  &-inner-text {\n    padding: 0 @divider-text-space;\n    flex: 0 0 auto;\n  }\n}\n","@keyframes shakeHead {\n  0%,\n  100% {\n    transform: translate3d(0, 0, 0);\n  }\n\n  25%,\n  75% {\n    transform: translate3d(-10px, 0, 0);\n  }\n\n  50% {\n    transform: translate3d(10px, 0, 0);\n  }\n}\n","@import '../../styles/common';\n@import '../../Modal/styles/animation';\n@import 'mixin';\n\n.rs-drawer-wrapper {\n  position: fixed;\n  z-index: @zindex-drawer;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n}\n\n// Container that the drawer scrolls within\n.rs-drawer {\n  display: none;\n  overflow: hidden;\n  position: fixed;\n  z-index: @zindex-drawer;\n  box-shadow: var(--rs-drawer-shadow);\n  // Prevent Chrome on Windows from adding a focus outline. For details, see\n  // https://github.com/twbs/bootstrap/pull/10951.\n  outline: 0;\n\n  // Kill the scroll on the body\n  &-open&-has-backdrop {\n    overflow: hidden;\n  }\n\n  // Drawer sizes\n  &-left,\n  &-right {\n    top: 0;\n    height: 100%;\n\n    &.rs-drawer-lg {\n      .drawer-content-width(@drawer-horizontal-lg);\n    }\n\n    &.rs-drawer-md {\n      .drawer-content-width(@drawer-horizontal-md);\n    }\n\n    &.rs-drawer-sm {\n      .drawer-content-width(@drawer-horizontal-sm);\n    }\n\n    &.rs-drawer-xs {\n      .drawer-content-width(@drawer-horizontal-xs);\n    }\n  }\n\n  &-top,\n  &-bottom {\n    width: 100%;\n\n    &.rs-drawer-lg {\n      .drawer-content-height(@drawer-vertical-lg);\n    }\n\n    &.rs-drawer-md {\n      .drawer-content-height(@drawer-vertical-md);\n    }\n\n    &.rs-drawer-sm {\n      .drawer-content-height(@drawer-vertical-sm);\n    }\n\n    &.rs-drawer-xs {\n      .drawer-content-height(@drawer-vertical-xs);\n    }\n  }\n\n  // Full page\n  &-full {\n    @max-width: ~'calc(100% - @{drawer-full-size-margin})';\n    @max-height: @max-width;\n\n    &.rs-drawer-top,\n    &.rs-drawer-bottom {\n      .drawer-content-height(@max-height);\n\n      .rs-drawer-content {\n        height: 100%;\n      }\n    }\n\n    &.rs-drawer-left,\n    &.rs-drawer-right {\n      .drawer-content-width(@max-width);\n    }\n  }\n\n  // Drawer directions\n  &-right {\n    right: 0;\n  }\n\n  &-left {\n    left: 0;\n  }\n\n  &-top {\n    top: 0;\n  }\n\n  &-bottom {\n    bottom: 0;\n  }\n}\n\n// Make sure drawer can be show in Safari.\n.rs-drawer-open .rs-drawer {\n  overflow: visible;\n}\n\n.rs-drawer-dialog {\n  position: relative;\n  width: 100%;\n  height: 100%;\n\n  .rs-drawer-shake & {\n    animation: 0.3s linear shakeHead;\n  }\n}\n\n.rs-drawer-content {\n  position: absolute;\n  background-color: var(--rs-bg-overlay);\n  outline: 0;\n  width: 100%;\n  height: 100%;\n\n  .high-contrast-mode({\n    background-color: var(--rs-drawer-bg);\n  });\n}\n\n.rs-drawer-backdrop {\n  position: fixed;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  z-index: @zindex-drawer-background;\n  background-color: var(--rs-bg-backdrop);\n\n  // Fade for backdrop\n  &.rs-anim-fade {\n    opacity: 0;\n    transition: opacity 0.3s ease-in;\n  }\n\n  &.rs-anim-in {\n    opacity: 1;\n  }\n}\n\n// Drawer header\n// Top section of the modal title.\n.rs-drawer-header {\n  .clearfix();\n\n  position: relative;\n  padding: 20px 40px 20px 60px;\n  border-bottom: 1px solid var(--rs-border-primary);\n  display: flex;\n\n  .rs-drawer-header-close {\n    position: absolute;\n    left: 15px;\n    top: 23px;\n  }\n}\n\n.rs-drawer-title {\n  flex-grow: 1;\n  flex-shrink: 1;\n  margin: 0;\n  color: var(--rs-text-heading);\n  font-weight: normal;\n  font-size: @modal-title-font-size;\n  line-height: 36px; // same as default button height\n  .ellipsis-basic();\n}\n\n.rs-drawer-actions {\n  .clearfix(); // clear it in case folks use .pull-* classes on buttons\n\n  flex-shrink: 0;\n  text-align: right; // right align buttons\n  border-top: none;\n  margin-left: auto;\n\n  .rs-drawer-title ~ & {\n    margin-left: 10px;\n  }\n\n  // Properly space out buttons\n  .rs-btn + .rs-btn {\n    margin-left: 10px;\n    margin-bottom: 0; // account for input[type=\"submit\"] which gets the bottom margin like all other inputs\n  }\n  // but override that for button groups\n  .rs-btn-group .rs-btn + .rs-btn {\n    margin-left: -1px;\n  }\n  // and override it for block buttons as well\n  .rs-btn-block + .rs-btn-block {\n    margin-left: 0;\n  }\n}\n\n// Drawer body\n.rs-drawer-body {\n  position: relative;\n  padding: 30px 60px;\n  height: 100%;\n  overflow: auto;\n\n  .rs-drawer-header + & {\n    height: calc(100% - 76px);\n  }\n\n  &-close {\n    position: absolute;\n    left: 15px;\n    top: 25px;\n\n    .rs-drawer-header ~ .rs-drawer-body & {\n      display: none;\n    }\n  }\n}\n\n// Measure scrollbar width for padding body during modal show/hide\n.rs-drawer-scrollbar-measure {\n  position: absolute;\n  top: -9999px;\n  width: 50px;\n  height: 50px;\n  overflow: scroll;\n}\n\n// Footer (for actions)\n// todo rs-drawer-footer has been deprecated, remove the styles once the component API is removed\n.rs-drawer-footer {\n  .clearfix(); // clear it in case folks use .pull-* classes on buttons\n\n  text-align: right; // right align buttons\n  border-top: none;\n  margin: 0 @drawer-content-spacing @drawer-content-spacing;\n\n  // Properly space out buttons\n  .rs-btn + .rs-btn {\n    margin-left: 10px;\n    margin-bottom: 0; // account for input[type=\"submit\"] which gets the bottom margin like all other inputs\n  }\n  // but override that for button groups\n  .rs-btn-group .rs-btn + .rs-btn {\n    margin-left: -1px;\n  }\n  // and override it for block buttons as well\n  .rs-btn-block + .rs-btn-block {\n    margin-left: 0;\n  }\n}\n",".drawer-content-width(@width) {\n  width: @width;\n}\n\n.drawer-content-height(@height) {\n  height: @height;\n}\n\n.drawer-animation(@timing) {\n  animation-duration: 0.3s;\n  animation-timing-function: @timing;\n  animation-fill-mode: forwards;\n}\n\n.drawer-animation-setting(@move-in-animation-name,@move-out-animation-name) {\n  &.rs-slide-out {\n    animation-name: @move-out-animation-name;\n  }\n\n  &.rs-slide-in {\n    animation-name: @move-in-animation-name;\n  }\n}\n","@import '../../styles/common';\n@import '../../styles/mixins/menu';\n@import 'mixin';\n@import '../../Button/styles/index.less';\n\n//\n// Dropdown menus\n// --------------------------------------------------\n\n.rs-dropdown-toggle-caret {\n  .dropdown-toggle-caret-common();\n}\n\n// The dropdown wrapper (div)\n.rs-dropdown {\n  position: relative;\n  display: inline-block;\n  vertical-align: middle; // Make sure dropdown vertical alignment with button,button group and so on.\n\n  // Dropdown button style\n  .rs-btn {\n    > .rs-dropdown-toggle-icon {\n      margin-right: 6px;\n    }\n\n    > .rs-dropdown-toggle-caret {\n      margin-left: 4px;\n    }\n  }\n\n  &-disabled &-toggle.rs-btn {\n    cursor: @cursor-disabled !important;\n    box-shadow: none;\n\n    &.rs-btn-subtle {\n      &,\n      &:focus,\n      &:active {\n        color: var(--rs-btn-subtle-disabled-text);\n        background: none;\n      }\n    }\n\n    &.rs-btn-primary {\n      &,\n      &:focus,\n      &:active {\n        opacity: 0.3;\n        background-color: var(--rs-btn-primary-bg);\n      }\n    }\n\n    &.rs-btn-link {\n      &,\n      &:focus,\n      &:active {\n        opacity: 0.3;\n        text-decoration: none;\n      }\n    }\n\n    &.rs-btn-ghost {\n      &,\n      &:focus,\n      &:active {\n        opacity: 0.3;\n      }\n    }\n\n    .rs-ripple-pond {\n      display: none !important;\n    }\n  }\n}\n\n.rs-dropdown-toggle {\n  .dropdown-toggle-size();\n\n  &,\n  &.rs-btn {\n    // Force set style even if has `.btn`.\n    .dropdown-toggle();\n  }\n\n  &.rs-dropdown-toggle-no-caret {\n    padding-right: @padding-x;\n  }\n}\n\n// The dropdown menu (ul)\n.rs-dropdown-menu {\n  margin: 0; // override default ul\n  list-style: none;\n  font-size: @font-size-base;\n  text-align: left; // Ensures proper alignment if parent has it changed (e.