dding-bottom: 13px;\n  }\n\n  &-sm {\n    padding-top: 8px;\n    padding-bottom: 8px;\n  }\n}\n\n/* rtl:begin:ignore */\n/* stylelint-disable-next-line */ // For RTL Scope\n.rs-list-item {\n  background-color: var(--rs-list-bg);\n  box-shadow: 0 -1px 0 var(--rs-list-border), 0 1px 0 var(--rs-list-border);\n\n  &-helper {\n    position: absolute;\n    background-color: var(--rs-list-bg);\n    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.1);\n    width: calc(100% - 2px);\n    border-radius: @border-radius;\n    top: 0;\n    left: 0;\n    z-index: @zindex-list-helper;\n    border: none;\n    cursor: move;\n\n    // Increase z-index when modal opened.\n    .rs-modal-open & {\n      z-index: (@zindex-modal + @zindex-list-helper);\n    }\n\n    // Increase z-index when drawer opened.\n    .rs-drawer-open & {\n      z-index: (@zindex-drawer + @zindex-list-helper);\n    }\n  }\n\n  &-holder {\n    background-color: var(--rs-list-placeholder-bg);\n    visibility: hidden;\n\n    &::after {\n      content: '';\n      position: absolute;\n      visibility: visible;\n      top: @list-border-width;\n      left: @list-border-width;\n      width: calc(100% - 2 * @list-border-width);\n      height: calc(100% - 2 * @list-border-width);\n      border: @list-border-width var(--rs-list-placeholder-border) dashed;\n    }\n  }\n}\n\n/* rtl:end:ignore */\n","@import '../../styles/common';\n@import 'mixin';\n\n.rs-loader {\n  .clearfix();\n  .loader-size(@loader-default-diameter, @loader-default-content-font-size);\n\n  display: inline-block;\n\n  //Loader spin\n  &-spin {\n    position: relative;\n    display: inline-block;\n    float: left;\n\n    .loader-spin();\n  }\n\n  //Loader spin\n  &-content {\n    float: left;\n    display: inline-block;\n  }\n\n  &-spin + &-content {\n    margin-left: @loader-content-spin-spacing-horizontal;\n\n    //Extra small size loader spacing is smaller.\n    .rs-loader-xs & {\n      margin-left: @loader-content-spin-spacing-horizontal-xs;\n    }\n  }\n\n  // Backdrop\n  &-backdrop {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background: var(--rs-loader-backdrop);\n  }\n\n  // Vertical\n  &-vertical {\n    width: auto;\n    display: inline-block;\n\n    .rs-loader {\n      flex-direction: column;\n    }\n\n    .rs-loader-spin {\n      margin: 0 auto;\n    }\n\n    .rs-loader-content {\n      line-height: unit((@line-height-computed / @loader-default-content-font-size));\n      text-align: center;\n    }\n\n    .rs-loader-spin,\n    .rs-loader-content {\n      display: block;\n      float: none;\n    }\n\n    .rs-loader-spin + .rs-loader-content {\n      margin-left: 0;\n      margin-top: @loader-content-spin-spacing-vertical;\n    }\n  }\n\n  // inverse\n  &-inverse {\n    .rs-loader-content {\n      color: var(--rs-text-inverse);\n    }\n\n    .rs-loader-backdrop {\n      background: var(--rs-loader-backdrop-inverse);\n    }\n\n    .rs-loader-spin::before {\n      border-color: var(--rs-loader-ring-inverse);\n    }\n\n    .rs-loader-spin::after {\n      border-top-color: var(--rs-loader-rotor-inverse);\n    }\n  }\n\n  // Loader speeds\n  &-speed-fast &-spin::after {\n    animation-duration: @loader-duration-fast;\n  }\n\n  &-speed-normal &-spin::after {\n    animation-duration: @loader-duration-normal;\n  }\n\n  &-speed-slow &-spin::after {\n    animation-duration: @loader-duration-slow;\n  }\n\n  // Position center\n  &-center,\n  &-backdrop-wrapper {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    margin: auto;\n    width: 100%;\n    height: 100%;\n\n    .rs-loader {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      width: 100%;\n      height: 100%;\n    }\n\n    // horizontal loader need set display table to set width auto\n    &:not(.rs-loader-vertical) {\n      .rs-loader {\n        display: flex;\n      }\n    }\n\n    // vertical content text align center\n    &.rs-loader-vertical {\n      .rs-loader {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        width: 100%;\n        height: 100%;\n      }\n    }\n\n    .rs-loader-content {\n      z-index: 1;\n    }\n  }\n\n  //Sizes\n  &-lg {\n    .loader-size(@loader-lg-diameter, @loader-lg-content-font-size);\n  }\n\n  &-md {\n    .loader-size(@loader-md-diameter, @loader-md-content-font-size);\n  }\n\n  &-sm {\n    .loader-size(@loader-sm-diameter, @loader-sm-content-font-size);\n  }\n\n  &-xs {\n    .loader-size(@loader-xs-diameter, @loader-xs-content-font-size);\n  }\n}\n\n// Set loader width auto\n.rs-loader-wrapper {\n  display: inline-block;\n  width: auto;\n  // Clear white space\n  line-height: 0;\n}\n\n@keyframes loaderSpin {\n  from {\n    transform: rotate(0);\n  }\n\n  to {\n    transform: rotate(360deg);\n  }\n}\n",".set-side-length(@side) {\n  width: @side;\n  height: @side;\n\n  &::before,\n  &::after {\n    width: @side;\n    height: @side;\n  }\n}\n\n.loader-size(@diameter,@font-size) {\n  .rs-loader {\n    &-spin {\n      .set-side-length(@diameter);\n    }\n\n    &-content {\n      font-size: @font-size;\n      line-height: unit((@diameter / @font-size));\n    }\n\n    &-vertical & {\n      height: (@diameter + @loader-content-spin-spacing-vertical + @line-height-computed);\n    }\n\n    &-vertical &-content {\n      line-height: unit((@line-height-computed / @font-size));\n    }\n  }\n}\n\n.loader-spin() {\n  &::before,\n  &::after {\n    content: '';\n    position: absolute;\n    left: 0;\n    right: 0;\n    display: block;\n    border-radius: 50%;\n  }\n\n  &::before {\n    border: @loader-spin-ring-width solid var(--rs-loader-ring);\n  }\n\n  &::after {\n    border-width: @loader-spin-ring-width;\n    border-style: solid;\n    border-color: var(--rs-loader-rotor) transparent transparent;\n    animation: loaderSpin @loader-duration-normal infinite linear;\n  }\n}\n","@keyframes notificationMoveIn {\n  0% {\n    opacity: 0;\n    transform-origin: 0% 0%;\n    transform: scaleY(0.8);\n  }\n\n  100% {\n    opacity: 1;\n    transform-origin: 0% 0%;\n    transform: scaleY(1);\n  }\n}\n\n@keyframes notificationMoveInLeft {\n  from {\n    opacity: 0;\n    transform: translate3d(-100%, 0, 0);\n  }\n\n  to {\n    opacity: 1;\n    transform: none;\n  }\n}\n\n@keyframes notificationMoveInRight {\n  from {\n    opacity: 0;\n    transform: translate3d(100%, 0, 0);\n  }\n\n  to {\n    opacity: 1;\n    transform: none;\n  }\n}\n\n@keyframes notificationMoveOut {\n  0% {\n    opacity: 1;\n    transform-origin: 0% 0%;\n    transform: scaleY(1);\n    max-height: 100px;\n  }\n\n  100% {\n    opacity: 0;\n    transform-origin: 0% 0%;\n    transform: scaleY(0.8);\n    max-height: 0;\n    overflow: hidden;\n  }\n}\n","@import '../../styles/common';\n@import '../../toaster/styles/animation';\n@import 'mixin';\n\n//\n// Messages\n// --------------------------------------------------\n\n// Base styles\n// -------------------------\n\n.rs-message {\n  border-radius: @message-border-radius;\n  font-size: @font-size-base;\n  line-height: @line-height-base;\n  position: relative;\n  animation-fill-mode: forwards;\n\n  &-container {\n    padding: @message-padding;\n    padding-right: @message-padding + 8px;\n    display: flex;\n  }\n\n  // Icon wrapper\n  &-icon-wrapper {\n    align-self: center;\n    font-size: 0; // remove whitespace before svg\n    margin-right: 10px;\n\n    // Icon\n    .rs-icon {\n      font-size: @message-icon-size;\n      align-items: center;\n    }\n  }\n\n  &-content {\n    flex-grow: 1;\n  }\n\n  // Header\n  &-header {\n    color: var(--rs-text-heading);\n    line-height: unit((round((@message-icon-large-size / @message-title-size), 16)));\n    .ellipsis();\n\n    & + .rs-message-body {\n      margin-top: 4px;\n    }\n  }\n\n  // Description\n  &-body {\n    color: var(--rs-text-primary);\n  }\n\n  // Message with a title\n  &.rs-message-has-title {\n    // Bigger icon\n    .rs-message-icon-wrapper {\n      align-self: flex-start;\n\n      .rs-icon {\n        font-size: @message-icon-large-size;\n        line-height: 1;\n      }\n    }\n  }\n\n  // Multiple messages should have spacing\n  // Hiding Message haven't spacing.\n  &:not(.rs-message-hiding) + & {\n    margin-top: 10px;\n\n    //The last message hiding haven't clearance.\n    &.rs-message-hiding:last-child {\n      transition: margin-top 0.1s linear 0.3s;\n      margin-top: 0;\n    }\n  }\n\n  &.rs-message-hiding {\n    // The same with notification\n    animation: notificationMoveOut 0.3s ease-in forwards;\n  }\n\n  // In dark mode, links inside Message body shows underline by default\n  .dark-mode({\n    .rs-btn-link,\n    a {\n      color: inherit;\n      text-decoration: underline;\n    }\n  });\n}\n\n// Full the containers\n.rs-message-full {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  border-radius: 0;\n}\n\n// Alternate styles\n// -------------------------\n// Generate contextual modifier classes for colorizing the message.\n\n.rs-message-success {\n  // Don't write arguments in single line when using var()\n  // it breaks stylelint\n  .message-variant(\n    var(--rs-message-success-bg);\n    var(--rs-message-success-header);\n    var(--rs-message-success-text);\n    var(--rs-message-success-icon);\n  );\n\n  .high-contrast-mode({\n    border: 1px solid var(--rs-message-success-border);\n  });\n}\n\n.rs-message-info {\n  .message-variant(\n    var(--rs-message-info-bg);\n    var(--rs-message-info-header);\n    var(--rs-message-info-text);\n    var(--rs-message-info-icon);\n  );\n\n  .high-contrast-mode({\n    border: 1px solid var(--rs-message-info-border);\n  });\n}\n\n.rs-message-warning {\n  .message-variant(\n    var(--rs-message-warning-bg);\n    var(--rs-message-warning-header);\n    var(--rs-message-warning-text);\n    var(--rs-message-warning-icon);\n  );\n\n  .high-contrast-mode({\n    border: 1px solid var(--rs-message-warning-border);\n  });\n}\n\n.rs-message-error {\n  .message-variant(\n    var(--rs-message-error-bg);\n    var(--rs-message-error-header);\n    var(--rs-message-error-text);\n    var(--rs-message-error-icon);\n  );\n\n  .high-contrast-mode({\n    border: 1px solid var(--rs-message-error-border);\n  });\n}\n","// Messages\n\n.message-variant(@background; @header-color; @text-color; @icon-color) {\n  background-color: @background;\n\n  .rs-message-header {\n    color: @header-color;\n  }\n\n  .rs-message-body {\n    color: @text-color;\n  }\n\n  .rs-message-icon-wrapper > .rs-icon,\n  .rs-btn-close {\n    color: @icon-color;\n  }\n}\n","@import '../../styles/common';\n@import 'mixin';\n\n//\n// Modals\n// --------------------------------------------------\n\n// Modal background\n.rs-modal-backdrop {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100vw;\n  height: 100vh;\n  z-index: @zindex-modal - 1;\n  background-color: var(--rs-bg-backdrop);\n\n  // Fade for backdrop\n  &.rs-anim-fade {\n    opacity: 0;\n    transition: opacity 0.3s ease-in;\n  }\n\n  &.rs-anim-in {\n    opacity: 1;\n  }\n}\n\n// Kill the scroll on the body\n.rs-modal-open {\n  overflow: hidden;\n}\n\n.rs-modal-wrapper {\n  position: fixed;\n  overflow: auto;\n  z-index: @zindex-modal;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n}\n\n// Container that the modal scrolls within\n.rs-modal {\n  display: none;\n  overflow: visible;\n  // Prevent Chrome on Windows from adding a focus outline. For details, see\n  // https://github.com/twbs/bootstrap/pull/10951.\n  outline: 0;\n  margin: 30px auto 0 auto;\n  position: relative;\n  width: auto;\n  z-index: @zindex-modal;\n\n  // Scale up the modal\n  &-lg {\n    .content-width(@modal-lg);\n  }\n\n  &-md {\n    .content-width(@modal-md);\n  }\n\n  &-sm {\n    .content-width(@modal-sm);\n  }\n\n  &-xs {\n    .content-width(@modal-xs);\n  }\n\n  &-full {\n    @modal-full-size-margin-value: @modal-full-size-margin* 2;\n    .content-width(~'calc(100% - @{modal-full-size-margin-value})');\n  }\n}\n\n.rs-modal-shake .rs-modal-dialog {\n  animation: 0.3s linear shakeHead;\n}\n\n// Actual modal\n.rs-modal-content {\n  position: relative;\n  background-color: var(--rs-bg-overlay);\n  border: var(--rs-modal-border, none);\n  border-radius: @modal-border-radius;\n  // Remove focus outline from opened modal\n  outline: 0;\n  box-shadow: var(--rs-modal-shadow);\n  padding: @modal-content-padding;\n}\n\n// Modal header\n// Top section of the modal w/ title and dismiss\n.rs-modal-header {\n  .clearfix();\n\n  padding-right: @line-height-computed;\n\n  .rs-modal-header-close {\n    // button width the same to height\n    @padding-right: 4px;\n\n    position: absolute;\n    right: @modal-content-padding;\n    top: @modal-content-padding;\n    font-size: @modal-close-btn-size;\n    line-height: @modal-close-btn-line-height;\n    color: @modal-close-btn-color;\n    width: @line-height-computed;\n    padding: 0 @padding-right;\n  }\n}\n\n.rs-modal-title {\n  font-weight: normal;\n  font-size: @modal-title-font-size;\n  line-height: unit((@line-height-computed / @modal-title-font-size));\n  width: 100%;\n  display: block;\n  color: var(--rs-text-heading);\n  margin: 0;\n  .ellipsis();\n}\n\n// Modal body\n// Where all modal content resides (sibling of .modal-header and .modal-footer)\n.rs-modal-body {\n  position: relative;\n  margin-top: @modal-body-margin;\n  // Make sure input error message don't overflow.\n  padding-bottom: @modal-body-margin;\n}\n\n// Footer (for actions)\n.rs-modal-footer {\n  text-align: right; // right align buttons\n  border-top: none;\n  .clearfix(); // clear it in case folks use .pull-* classes on buttons\n\n  // Properly space out buttons\n  .rs-btn + .rs-btn {\n    margin-left: 10px;\n    margin-bottom: 0; // account for input[type=\"submit\"] which gets the bottom margin like all other inputs\n  }\n  // but override that for button groups\n  .rs-btn-group .rs-btn + .rs-btn {\n    margin-left: -1px;\n  }\n  // and override it for block