 of the  1 pixel white border in Edge/IE11 browser .\n  .ie11-edge-direction(right, -(@size+1px));\n\n  margin-top: -@size;\n  border-width: @size 0 @size @size;\n  border-left-color: @color;\n}\n\n.popover-arrow(left, @size, @color) {\n  left: -@size;\n  // FIXED: The problem of the  1 pixel white border in Edge/IE11 browser .\n  .ie11-edge-direction(left, -(@size+1px));\n\n  margin-top: -@size;\n  border-width: @size @size @size 0;\n  border-right-color: @color;\n}\n\n/* rtl:end:ignore */\n\n// Append Popover's arrow to Popover\n// @direction: The arrow's pointing direction. It's usually opposite of popover's placement\n//             e.g. popover[placement=\"top\"] has an arrow[direction=\"bottom\"]\n// @size:      The arrow's size (height)\n// @bg:        The arrow's background color, usually equal to popover's background\n// @border:    The arrow's border color, usually equal to popover's border (which only take effect\n//             in high-contrast theme)\n.with-popover-arrow(@direction, @size: @popover-arrow-width, @bg: var(--rs-bg-overlay), @border: var(--rs-border-primary)) {\n  &::after {\n    .popover-arrow(@direction, @size, @bg);\n  }\n\n  .high-contrast-mode({\n    &::before {\n      .popover-arrow(@direction, @size + 1px, @border);\n    }\n  });\n}\n","\n@keyframes progress-active {\n  0% {\n    opacity: 0.1;\n    width: 0;\n  }\n\n  20% {\n    opacity: 0.5;\n    width: 0;\n  }\n\n  100% {\n    opacity: 0;\n    width: 100%;\n  }\n}\n\n@keyframes progress-active-vertical {\n  0% {\n    opacity: 0.1;\n    height: 0;\n  }\n\n  20% {\n    opacity: 0.5;\n    height: 0;\n  }\n\n  100% {\n    opacity: 0;\n    height: 100%;\n  }\n}\n","@import '../../styles/common';\n@import 'animation';\n\n.rs-progress {\n  // Remove whitespace.\n  &-info {\n    font-size: 0;\n  }\n\n  // Set base font-size && reset font-size\n  &,\n  &-icon-success,\n  &-icon-fail,\n  &-info-status {\n    font-size: @progress-font-size;\n    line-height: unit((@line-height-computed / @progress-font-size));\n  }\n}\n\n.rs-progress-circle {\n  position: relative;\n  font-size: 0;\n\n  &-info {\n    @info-height: 36px;\n\n    position: absolute;\n    margin: auto;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    text-align: center;\n    font-size: @progress-font-size;\n    line-height: unit((@info-height / @progress-font-size));\n    height: @info-height;\n    color: var(--rs-text-secondary);\n  }\n\n  .rs-progress-trail {\n    stroke: var(--rs-progress-bg);\n  }\n\n  .rs-progress-stroke {\n    stroke: var(--rs-progress-bar);\n    transition: stroke-dashoffset 0.3s ease, stroke-dasharray 0.2s ease-in-out, stroke 0.3s;\n  }\n\n  // Setting status color\n  &-success &-info {\n    color: var(--rs-progress-bar-success);\n  }\n\n  &-success .rs-progress-stroke {\n    stroke: var(--rs-progress-bar-success);\n  }\n\n  &-fail &-info {\n    color: var(--rs-progress-bar-fail);\n  }\n\n  &-fail .rs-progress-stroke {\n    stroke: var(--rs-progress-bar-fail);\n  }\n}\n\n.rs-progress-line {\n  display: flex;\n  align-items: center;\n  width: 100%;\n  padding: @progress-line-padding-vertical @progress-line-padding-horizontal;\n\n  &-active &-bg {\n    position: relative;\n\n    &::before {\n      content: '';\n      opacity: 0;\n      position: absolute;\n      top: 0;\n      left: 0;\n      right: 0;\n      bottom: 0;\n      background-color: #fff;\n      border-radius: 10px;\n      animation: progress-active 2s cubic-bezier(0.23, 1, 0.32, 1) infinite;\n    }\n  }\n\n  .rs-progress-info {\n    width: 50px;\n    flex: 0 0 50px;\n    height: 20px;\n    padding-left: @progress-element-gap;\n    display: flex;\n    align-items: center;\n  }\n\n  &-outer {\n    flex: 1 1 auto;\n    border-radius: (@progress-bar-height / 2);\n    overflow: hidden;\n    // Make sure outer vertical-align:center;\n    vertical-align: middle;\n  }\n\n  &-inner {\n    background: var(--rs-progress-bg);\n    border-radius: (@progress-bar-height / 2);\n  }\n\n  &-bg {\n    background-color: var(--rs-progress-bar);\n    border-radius: (@progress-bar-height / 2);\n    transition: @progress-bg-transition;\n    transition-property: width, background-color;\n  }\n\n  &-inner,\n  &-bg {\n    height: @progress-bar-height;\n  }\n\n  // Setting status color\n  &-success {\n    color: var(--rs-progress-bar-success);\n  }\n\n  &-success &-bg {\n    background-color: var(--rs-progress-bar-success);\n  }\n\n  &-fail {\n    color: var(--rs-progress-bar-fail);\n  }\n\n  &-fail &-bg {\n    background-color: var(--rs-progress-bar-fail);\n  }\n\n  &&-vertical {\n    flex-direction: column-reverse;\n    width: auto;\n  }\n\n  &&-vertical &-outer {\n    width: auto;\n    flex: 1 1 auto;\n  }\n\n  &&-vertical &-inner {\n    min-height: 100px;\n    width: @progress-bar-height;\n    display: flex;\n    flex-direction: column-reverse;\n    align-items: center;\n    position: relative;\n    height: 100%;\n  }\n\n  &&-vertical .rs-progress-info {\n    flex-basis: auto;\n    padding-left: 0;\n    width: auto;\n  }\n\n  &&-vertical &-bg {\n    width: 100%;\n    transition-property: height, background-color;\n    transform: rotate(180deg);\n    position: absolute;\n    bottom: 0;\n  }\n\n  &&-vertical&-active &-bg::before {\n    animation-name: progress-active-vertical;\n  }\n}\n\n// Status icons styles\n.rs-progress-icon-success,\n.rs-progress-icon-fail {\n  display: inline-block;\n\n  svg {\n    // x2= 1px\n    .icon-bolder(0.5px);\n  }\n\n  .rs-progress-line & {\n    line-height: 0;\n    font-size: 14px;\n  }\n\n  .rs-progress-circle & {\n    line-height: 0;\n    font-size: 36px;\n  }\n}\n","@import '../../styles/common';\n@import '../../Checkbox/styles/mixin';\n@import '../../Button/styles/mixin';\n@import 'mixin';\n\n.rs-radio {\n  .radio-checkbox-common();\n\n  [type='radio']:focus ~ .rs-radio-inner::before {\n    box-shadow: var(--rs-state-focus-shadow);\n  }\n}\n\n.rs-radio-checker {\n  padding-top: 10px;\n  padding-bottom: 10px;\n  padding-left: (@radio-sense-width * 2 + @radio-width-height);\n  min-height: (@radio-sense-width * 2 + @radio-width-height);\n  line-height: 1;\n  position: relative;\n}\n\n.rs-radio-wrapper {\n  position: absolute;\n  width: @radio-width-height;\n  height: @radio-width-height;\n  display: inline-block;\n  left: @radio-sense-width;\n  top: @checkbox-sense-width;\n\n  [type='radio'] {\n    opacity: 0;\n    // bring to foreground\n    position: absolute;\n    z-index: 1;\n    width: @radio-width-height;\n    height: @radio-width-height;\n    cursor: pointer;\n  }\n\n  &::before,\n  &::after,\n  .rs-radio-inner::before,\n  .rs-radio-inner::after {\n    content: '';\n    position: absolute;\n    left: 0;\n    top: 0;\n    display: block;\n  }\n\n  &::before,\n  .rs-radio-inner::before {\n    width: @radio-width-height;\n    height: @radio-width-height;\n  }\n\n  // Ripple effect area.\n  &::before {\n    .radio-inner-wrapper(var(--rs-radio-checked-bg));\n\n    transform: scale(1);\n    opacity: 0.7;\n    visibility: hidden;\n    transition: transform 0.2s linear, opacity 0.2s linear;\n\n    .high-contrast-mode({\n      transition: none;\n    });\n\n    .rs-radio-checked & {\n      transform: scale(1.5);\n      opacity: 0;\n      visibility: visible;\n    }\n  }\n\n  // Sense area(Can be clicked).\n  &::after {\n    top: -@radio-sense-width;\n    right: -@radio-sense-width;\n    bottom: -@radio-sense-width;\n    left: -@radio-sense-width;\n  }\n}\n\n// Out border.\n.rs-radio-inner {\n  &::before {\n    .radio-inner-wrapper(var(--rs-radio-border));\n\n    transition: background 0.2s linear, border 0.2s linear, box-shadow 0.2s linear;\n\n    .high-contrast-mode({\n      transition: none;\n    });\n\n    .rs-radio:hover & {\n      border-color: var(--rs-radio-checked-bg);\n    }\n\n    .rs-radio.rs-radio-disabled & {\n      border-color: var(--rs-radio-disabled-bg);\n      background-color: var(--rs-radio-disabled-bg);\n    }\n\n    .rs-radio.rs-radio-checked & {\n      border-color: var(--rs-radio-checked-bg);\n      background-color: var(--rs-radio-checked-bg);\n    }\n\n    .rs-radio.rs-radio-disabled.rs-radio-checked & {\n      opacity: 0.3;\n\n      .high-contrast-mode({\n        opacity: 0.5;\n      });\n    }\n  }\n\n  // Solid circle.\n  &::after {\n    width: @radio-inner-width-height;\n    height: @radio-inner-width-height;\n    background-color: var(--rs-radio-marker);\n    margin-top: ((@radio-width-height - @radio-inner-width-height) / 2);\n    margin-left: ((@radio-width-height - @radio-inner-width-height) / 2);\n    border-radius: @radio-border-radius;\n    opacity: 0;\n    transform: scale(0);\n    transition: opacity 0.2s ease-in, transform 0.2s cubic-bezier(0.12, 0.4, 0.29, 1.46);\n\n    .high-contrast-mode({\n      transition: none;\n    });\n\n    .rs-radio-checked & {\n      transform: scale(1);\n      opacity: 1;\n    }\n  }\n}\n",".radio-inner-wrapper(@border-color) {\n  border: 1px solid @border-color;\n  background-color: @radio-bg;\n  border-radius: @radio-border-radius;\n}\n","@import (reference) '../../Button/styles/index';\n\n.rs-radio-group {\n  display: flex;\n  flex-direction: column;\n}\n\n.rs-radio-group-inline {\n  flex-direction: row;\n  margin-left: -1 * @radio-sense-width;\n}\n\n// Radio Group - picker appearance\n.rs-radio-group-picker {\n  display: inline-flex;\n  color: var(--rs-text-primary);\n  border: 1px solid var(--rs-border-primary);\n  border-radius: @border-radius;\n  margin-left: 0;\n\n  .rs-radio-inline {\n    margin-left: 0;\n  }\n\n  .rs-radio-checker {\n    padding: 0;\n    min-height: auto;\n    border-bottom: 1px solid transparent;\n    margin: 0 10px;\n  }\n\n  // Picker Radio group hidden radio.\n  .rs-radio-wrapper {\n    display: none;\n  }\n\n  .rs-radio-checker > label {\n    // TODO: Should not inherit Button styles\n    .rs-btn();\n    .rs-btn-subtle();\n\n    color: var(--rs-text-secondary);\n    background: none;\n    transition: color 0.3s linear;\n    padding: (@padding-y - 1px) 0;\n\n    .high-contrast-mode({\n      transition: none;\n    });\n\n    &:hover,\n    &:active {\n      color: var(--rs-text-active);\n      background: none;\n    }\n  }\n\n  .rs-radio-checked .rs-radio-checker {\n    border-bottom: 2px solid var(--rs-text-active);\n\n    > label {\n      color: var(--rs-text-active);\n    }\n  }\n\n  .rs-radio-disabled .rs-radio-checker {\n    opacity: 0.3;\n    cursor: not-allowed;\n  }\n\n  .rs-radio-disabled:not(.rs-radio-checked) .rs-radio-checker > label {\n    color: var(--rs-text-secondary);\n  }\n}\n","@import '../../styles/common';\n\n.rs-ripple {\n  position: absolute;\n  display: block;\n  border-radius: 50%;\n  background-color: @ripple-bg;\n  transform: scale(0);\n  transition: 0s;\n\n  &-rippling {\n    transition: transform 0.55s cubic-bezier(0.4, 0, 0.2, 1),\n      opacity 1s cubic-bezier(0.4, 0, 0.2, 1);\n    transform: scale(1);\n    opacity: 0;\n  }\n\n  &-pond {\n    content: '';\n    display: block;\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n\n    & when (@enable-ripple-effect = false) {\n      display: none !important;\n    }\n\n    .high-contrast-mode({\n      display: none !important;\n    });\n  }\n}\n","@import '../../styles/common';\n@import '../../Grid/styles/mixin';\n@import '../../Grid/styles/grid-framework';\n\n// Row\n//\n// Rows contain and clear the floats of your columns.\n.rs-row {\n  .make-row();\n}\n","@import '../../styles/common';\n@import '../../styles/mixins/listbox';\n@import '../../Picker/styles/index';\n\n// Select Picker\n// ----------------------\n\n// Menu Items (the listbox)\n.rs-picker-select-menu-items {\n  .picker-menu-items-reset();\n  .picker-menu-group-common(picker);\n  .picker-menu-group-title(picker);\n  .picker-menu-group-closed(picker);\n}\n\n// Menu item (the option)\n.rs-picker-select-menu-item {\n  .listbox-option();\n\n  &:hover,\n  &:focus,\n  &&-focus {\n    .listbox-option-active();\n  }\n\n  &&-active {\n    .picker-item-active();\n\n    color: var(--rs-listbox-option-selected-text);\n    background-color: var(--rs-listbox-option-selected-bg);\n  }\n\n  &&-disabled {\n    color: var(--rs-listbox-option-disabled-text);\n    background: none;\n    cursor: @cursor-disabled;\n  }\n\n  .grouped &,\n  .rs-picker-select-menu-group-children & {\n    padding-left: @picker-group-children-padding-left;\n  }\n}\n","@import '../../Container/styles/index';\n\n.rs-sidebar-collapse {\n  transition: flex @sidenav-collapse-transition-config, width @sidenav-collapse-transition-config;\n  // Fixed firefox doesn't apply flex-basis if children width is so large.\n  // https://github.com/philipwalton/flexbugs/issues/41#issuecomment-111590394\n  min-width: 0;\n}\n","@import '../../styles/common';\n@import '../../styles//mixins/menu';\n@import '../../Ripple/styles/mixins';\n@import 'mixin';\n\n//\n// Sidenavs\n// --------------------------------------------------\n\n// Base class\n// --------------------------------------------------\n.rs-sidenav {\n  transition: width @sidenav-collapse-transition-config;\n  width: @sidenav-default-width;\n}\n\n.rs-sidenav-nav {\n  list-style: none;\n  padding: 0;\n\n  > .rs-sidenav-item,\n  > .rs-dropdown .rs-dropdown-toggle,\n  .rs-dropdown-item,\n  .rs-dropdown-item-toggle {\n    padding: @sidenav-padding-vertical @sidenav-padding-horizontal;\n    transition: @nav-item-transition;\n    border-radius: 0;\n    color: inherit;\n\n    .high-contrast-mode({\n      transition: none;\n    });\n  }\n\n  > .rs-sidenav-item,\n  > .rs-dropdown {\n    margin: 0 !important;\n  }\n\n  // <Dropdown> within <Sidenav>\n  > .rs-dropdown {\n    // Submenu toggle\n    .rs-dropdown-item-toggle {\n      display: block;\n      width: 100%;\n      text-align: start;\n      background: none;\n      padding-right: (@sidenav-padding-horizontal + @sidenav-dropdown-toggle-caret-width);\n      position: relative;\n\n      .rs-sidenav-collapse-in & {\n        padding-left: @sidenav-level2-retract;\n      }\n\n      &:focus {\n        outline: 0;\n      }\n\n      &:focus-visible {\n        .focus-ring(inset);\n\n        .high-contrast-mode({\n          .focus-ring(slim-inset);\n        });\n\n        z-index: 1;\n      }\n\n      // Submenu toggle icon\n      &-icon {\n        position: absolute;\n        right: @sidenav-padding-horizontal;\n        top: @sidenav-children-padding-vertical;\n        width: auto;\n        height: @sidenav-dropdown-toggle-caret-width;\n        transform: rotate(90deg);\n      }\n    }\n\n    // Expanded submenu toggle icon\n    .rs-dropdown-item-expand .rs-dropdown-item-toggle-icon {\n      transform: rotate(270deg);\n    }\n  }\n\n  .high-contrast-mode({\n    .rs-dropdown-toggle,\n    .rs-dropdown-menu {\n      border: none;\n    }\n  });\n\n  .rs-sidenav-item:focus-visible {\n    .focus-ring(inset);\n\n    .high-contrast-mode({\n      .focus-ring(slim-inset);\n    });\n\n    z-index: 1;\n  }\n\n  > .rs-dropdown .rs-dropdown-menu-item-focus,\n  > .rs-dropdown .rs-dropdown-item-active {\n    font-weight: normal;\n    background: transparent;\n  }\n\n  > .rs-dropdown .rs-dropdown-menu {\n    transition: height @sidenav-collapse-transition-config;\n  }\n\n  > .rs-dropdown .rs-dropdown-toggle {\n    height: auto;\n    padding-right: (@sidenav-padding-horizontal + @sidenav-dropdown-toggle-caret-width);\n\n    .rs-dropdown-toggle-caret {\n      right: @sidenav-padding-horizontal;\n      top: @sidenav-padding-vertical;\n\n      // Make Caret AngleRight direction at down.\n      &[aria-label='angle-right'] {\n        transform: rotate(90deg);\n      }\n    }\n  }\n\n  // Level1\n  // -----------------\n  > .rs-sidenav-item,\n  > .rs-dropdown .rs-dropdown-toggle {\n    font-size: @sidenav-level1-item-font-size;\n    line-height: @sidenav-level1-item-line-height;\n    padding-left: (@sidenav-padding-horizontal + @sidenav-icon-width + @sidenav-icon-spacing);\n    width: 100%;\n    white-space: normal;\n\n    // Sidenav is usually placed by the left/right edge of the page\n    // thus use an inset focus ring to prevent overflow clipping\n    &:focus-visible {\n      .focus-ring(inset);\n    }\n\n    > .rs-icon:not(.rs-dropdown-toggle-caret) {\n      font-size: @sidenav-level1-item-font-size;\n      margin-right: @sidenav-icon-spacing;\n      position: absolute;\n      left: @sidenav-padding-horizontal;\n      top: @sidenav-padding-vertical;\n      line-height: @sidenav-level1-item-line-height;\n      height: @sidenav-level1-item-font-size;\n    }\n  }\n\n  > .rs-dropdown-focus .rs-dropdown-toggle,\n  .rs-dropdown-toggle:focus-visible {\n    .focus-ring();\n\n    .high-contrast-mode({\n      .focus-ring(slim-inset);\n    });\n\n    z-index: 2;\n  }\n}\n\n.rs-sidenav-item {\n  position: relative;\n  display: block;\n  outline: 0;\n  overflow: hidden;\n\n  &:hover {\n    text-decoration: none;\n  }\n\n  &:focus-visible,\n  &&-focus {\n    .focus-ring(slim-inset);\n\n    z-index: 2;\n  }\n}\n\n// Expanded styles\n.rs-sidenav-collapse-in {\n  width: @sidenav-collapse-in-width;\n\n  //Nav item\n  .rs-sidenav-item {\n    border-radius: 0;\n  }\n\n  //Dropdown style rewrite\n  .rs-dropdown {\n    display: flex;\n    flex: 1 1 auto;\n    flex-direction: column;\n\n    &-item:focus-visible,\n    &-item-focus {\n      .focus-ring(inset);\n\n      background: none;\n      z-index: 2;\n\n      .high-contrast-mode({\n        .focus-ring(slim-inset);\n      });\n\n      &.rs-dropdown-item-submenu {\n        box-shadow: none;\n\n        > .rs-dropdown-item-toggle {\n          .focus-ring(inset);\n\n          .high-contrast-mode({\n            .focus-ring(slim-inset);\n          });\n\n          z-index: 2;\n        }\n      }\n    }\n\n    // Set  the ripple effect of items.\n    &-item:not(.rs-dropdown-item-submenu),\n    &-item-submenu > .rs-dropdown-item-toggle {\n      .with-ripple();\n    }\n\n    // Remove the ripple effect of disabled items.\n    &-item-disabled &-item::after {\n      display: none;\n    }\n\n    // Setting icon to dropdown-caret.\n    .rs-dropdown-toggle-caret::before,\n    .rs-dropdown-item-toggle > .rs-dropdown-item-toggle-icon::before {\n      display: none;\n    }\n\n    .rs-dropdown-toggle-caret,\n    .rs-dropdown-item-toggle > .rs-dropdown-item-toggle-icon {\n      transition: transform 0.3s linear;\n    }\n\n    // Expand\n    &.rs-dropdown-expand .rs-dropdown-toggle-caret {\n      transform: rotate(180deg);\n    }\n\n    // Dropdown menu\n    .rs-dropdown-menu {\n      .reset-sidenav-dropdown-menu();\n    }\n\n    // Submenu\n    .rs-dropdown-item-submenu {\n      padding: 0;\n\n      &:hover {\n        background-color: transparent;\n      }\n    }\n  }\n\n  //Ellipsis\n  .rs-dropdown-item:not(.rs-dropdown-item-submenu),\n  .rs-dropdown-menu .rs-dropdown-menu-toggle {\n    width: 100%;\n    white-space: normal;\n  }\n\n  // Expand && Collapse\n  // Collapse\n  .rs-dropdown-menu {\n    overflow: hidden;\n  }\n\n  .rs-dropdown.rs-dropdown-collapse > .rs-dropdown-menu.rs-dropdown-menu-collapse-out,\n  .rs-dropdown-item-submenu.rs-dropdown-item-collapse\n    > .rs-dropdown-menu.rs-dropdown-menu-collapse-out {\n    display: none;\n  }\n\n  .rs-dropdown.rs-dropdown-menu-collapse-in > .rs-dropdown-menu.dropdown-menu-collapse-in,\n  .rs-dropdown-item-submenu.rs-dropdown-menu-collapse-in\n    > .rs-dropdown-menu.rs-dropdown-menu-collapse-in {\n    display: block;\n  }\n\n  // @warn Here we can only use absolute positioning because of the limitations of using <Dropdown/> component.\n\n  .rs-dropdown-item-submenu {\n    > .rs-dropdown-item-menu-icon {\n      padding-top: @sidenav-children-padding-vertical;\n      padding-bottom: @sidenav-children-padding-vertical;\n      line-height: @line-height-base;\n      width: @font-size-base;\n      position: absolute;\n      z-index: 1;\n    }\n  }\n\n  // Level2\n  // -----------------\n  .rs-dropdown-item,\n  .rs-dropdown-item-submenu > .rs-dropdown-item-toggle {\n    padding: @sidenav-children-padding-vertical @sidenav-padding-horizontal\n      @sidenav-children-padding-vertical @sidenav-level2-retract;\n  }\n\n  .rs-dropdown-item {\n    .dropdown-menu-icon-left-position(@sidenav-level2-retract);\n  }\n\n  // Level3\n  // -----------------\n  .rs-dropdown-item .rs-dropdown-item:not(.rs-dropdown-item-submenu),\n  .rs-dropdown-item-submenu > .rs-dropdown-menu .rs-dropdown-item-toggle {\n    padding-left: @sidenav-level3-retract;\n  }\n\n  .rs-dropdown-item-submenu > .rs-dropdown-menu > .rs-dropdown-item {\n    .dropdown-menu-icon-left-position(@sidenav-level3-retract);\n  }\n}\n\n// Folded && Collapsing styles\n.rs-sidenav-collapse-out {\n  .rs-sidenav-item,\n  .rs-dropdown .rs-dropdown-toggle {\n    padding-right: 0;\n    // fixed height\n    height: @sidenav-item-height;\n    // supplement padding\n    padding-left: @sidenav-default-width;\n\n    &:focus-visible {\n      .focus-ring();\n\n      .high-contrast-mode({\n        .focus-ring(slim-inset);\n      });\n    }\n  }\n\n  // The same to the dropdown item\n  .rs-dropdown-item {\n    @padding-right: @dropdown-item-padding-x+ @dropdown-item-submenu-icon-angle-spacing +\n      @dropdown-item-submenu-icon-angle-width;\n\n    padding: @dropdown-item-padding-y @padding-right @dropdown-item-padding-y\n      @dropdown-item-padding-x;\n  }\n\n  .rs-dropdown-item-submenu {\n    padding: 0;\n  }\n\n  .rs-dropdown-toggle-caret {\n    display: none;\n  }\n\n  // Folded text animation setting\n  .rs-sidenav-item-text,\n  .rs-dropdown-toggle > span {\n    animation: sideNavFoldedText @sidenav-collapse-transition-config forwards;\n  }\n}\n\n// Collapsing styles\n.rs-sidenav-collapsing {\n  // Rewrite  text-overflow\n  &.rs-sidenav-collapse-in,\n  &.rs-sidenav-collapse-out {\n    .rs-sidenav-item,\n    .rs-dropdown .rs-dropdown-toggle,\n    .rs-dropdown-item,\n    .rs-dropdown-item-submenu > .rs-dropdown-menu-toggle {\n      white-space: nowrap;\n      text-overflow: clip;\n    }\n  }\n\n  &.rs-sidenav-collapse-in {\n    .rs-dropdown-toggle-caret {\n      display: none;\n    }\n  }\n\n  // Make sure icon text-align center\n  &.rs-sidenav-collapse-out {\n    .rs-dropdown .rs-dropdown-menu {\n      padding-left: @sidenav-default-width - @sidenav-level2-retract;\n    }\n\n    .rs-dropdown-item {\n      animation: sideNavFoldedText @sidenav-collapse-transition-config;\n    }\n  }\n}\n\n// Alternate Sidenav\n// --------------------------------------------------\n\n// Default Sidenav\n.rs-sidenav-default {\n  background-color: var(--rs-sidenav-default-bg);\n  color: var(--rs-sidenav-default-text);\n\n  .rs-sidenav-item,\n  .rs-dropdown-toggle {\n    background-color: var(--rs-sidenav-default-bg);\n  }\n\n  .rs-sidenav-item:hover,\n  .rs-dropdown .rs-dropdown-toggle:hover,\n  .rs-dropdown.rs-dropdown-open .rs-dropdown-toggle {\n    background-color: var(--rs-sidenav-default-hover-bg);\n    color: var(--rs-sidenav-default-hover-text);\n  }\n\n  .rs-sidenav-item.rs-sidenav-item-active,\n  .rs-dropdown-item.rs-dropdown-item-active,\n  .rs-dropdown.rs-dropdown-selected-within .rs-dropdown-toggle-icon {\n    color: var(--rs-sidenav-default-selected-text);\n  }\n\n  // Collapsed\n  &.rs-sidenav-collapse-out {\n    .rs-dropdown-item.rs-dropdown-item-active {\n      .menuitem-active();\n    }\n  }\n}\n\n.rs-sidenav-default.rs-sidenav-collapse-in,\n.rs-sidenav-default.rs-sidenav-collapsing {\n  .rs-dropdown-item > .rs-dropdown-menu-toggle,\n  .rs-dropdown-item > .rs-dropdown-item-toggle {\n    color: var(--rs-sidenav-default-text);\n\n    &:hover,\n    &:focus,\n    &.rs-dropdown-item-focus {\n      background-color: var(--rs-sidenav-default-hover-bg);\n      color: var(--rs-sidenav-default-hover-text);\n    }\n  }\n\n  // Reset active dropdown-item style\n  .rs-sidenav-item.rs-sidenav-item-active,\n  .rs-dropdown-menu > .rs-dropdown-item.rs-dropdown-item-active {\n    color: var(--rs-sidenav-default-selected-text);\n\n    .high-contrast-mode({\n      text-decoration: underline;\n    });\n  }\n}\n\n.rs-sidenav-default.rs-sidenav-collapse-in,\n.rs-sidenav-default.rs-sidenav-collapsing,\n.rs-sidenav-default.rs-sidenav-collapse-out {\n  // Set active Level1 style\n  .rs-dropdown.rs-dropdown-selected-within .rs-dropdown-toggle-icon {\n    color: var(--rs-sidenav-default-selected-text);\n  }\n}\n\n// Inverse Sidenav\n.rs-sidenav-inverse {\n  background-color: var(--rs-sidenav-inverse-bg);\n  color: var(--rs-sidenav-inverse-text);\n\n  .rs-sidenav-item,\n  .rs-dropdown .rs-dropdown-toggle,\n  .rs-dropdown-item-submenu:hover > .rs-dropdown-item-toggle {\n    background-color: var(--rs-sidenav-inverse-bg);\n\n    &:hover,\n    &:focus {\n      background-color: var(--rs-sidenav-inverse-hover-bg);\n    }\n  }\n\n  .rs-dropdown-item,\n  .rs-dropdown-item > .rs-dropdown-menu-toggle,\n  .rs-dropdown-item > .rs-dropdown-item-toggle {\n    color: var(--rs-sidenav-inverse-text);\n\n    &:hover,\n    &:focus,\n    &.rs-dropdown-item-focus {\n      background-color: var(--rs-sidenav-inverse-hover-bg);\n      color: var(--rs-sidenav-inverse-text);\n    }\n  }\n\n  // Active\n  .rs-sidenav-item.rs-sidenav-item-active,\n  .rs-dropdown.rs-dropdown-selected-within .rs-dropdown-toggle {\n    .high-contrast-mode({\n      color: var(--rs-sidenav-inverse-selected-text);\n    });\n  }\n\n  // Active\n  .rs-dropdown.rs-dropdown-selected-within .rs-dropdown-toggle,\n  .rs-sidenav-item,\n  .rs-dropdown .rs-dropdown-toggle {\n    color: var(--rs-sidenav-inverse-text);\n\n    &:hover,\n    &:focus,\n    &.rs-dropdown-item-focus {\n      color: var(--rs-sidenav-inverse-text);\n    }\n  }\n\n  .rs-sidenav-item.rs-sidenav-item-active,\n  .rs-dropdown-item.rs-dropdown-item-active {\n    background-color: var(--rs-sidenav-inverse-selected-bg);\n  }\n}\n\n// Subtle Sidenav\n.rs-sidenav-subtle {\n  background-color: transparent;\n  color: var(--rs-sidenav-subtle-text);\n\n  .rs-sidenav-item,\n  .rs-dropdown .rs-dropdown-toggle {\n    background-color: transparent;\n\n    &:hover,\n    &:focus {\n      background-color: transparent;\n      color: var(--rs-sidenav-subtle-hover-text);\n    }\n  }\n\n  // Active\n  .rs-sidenav-item.rs-sidenav-item-active,\n  .rs-dropdown-item.rs-dropdown-item-active,\n  .rs-dropdown.rs-dropdown-selected-within .rs-dropdown-toggle {\n    color: var(--rs-sidenav-subtle-selected-text);\n  }\n\n  .rs-dropdown-open > .rs-dropdown-toggle.btn {\n    background-color: transparent;\n\n    &:hover,\n    &:focus {\n      background-color: var(--rs-sidenav-subtle-hover-bg);\n    }\n  }\n}\n\n.rs-sidenav-subtle.rs-sidenav-collapse-in,\n.rs-sidenav-subtle.rs-sidenav-collapsing {\n  .rs-dropdown-item > .rs-dropdown-menu-toggle,\n  .rs-dropdown-item > .rs-dropdown-item-toggle {\n    color: var(--rs-sidenav-subtle-text);\n\n    &:hover,\n    &:focus {\n      background-color: var(--rs-sidenav-subtle-hover-bg);\n      color: var(--rs-sidenav-subtle-hover-text);\n    }\n  }\n\n  // Reset active dropdown-item style\n  .rs-dropdown-menu > .rs-dropdown-item.rs-dropdown-item-active {\n    color: var(--rs-sidenav-subtle-selected-text);\n  }\n}\n\n.rs-sidenav-subtle.rs-sidenav-collapse-in,\n.rs-sidenav-subtle.rs-sidenav-collapsing,\n.rs-sidenav-subtle.rs-sidenav-collapse-out {\n  // Set active Level1 style\n  .rs-dropdown.rs-dropdown-selected-within .rs-dropdown-toggle {\n    color: var(--rs-sidenav-subtle-text);\n\n    &:hover {\n      background-color: var(--rs-sidenav-subtle-hover-bg);\n    }\n\n    &-icon {\n      color: var(--rs-sidenav-subtle-selected-text);\n    }\n  }\n}\n\n@keyframes sideNavFoldedText {\n  0% {\n    max-width: 200px;\n    opacity: 0.8;\n  }\n\n  100% {\n    max-width: 0;\n    opacity: 0;\n  }\n}\n",".reset-sidenav-dropdown-menu() {\n  display: block;\n  position: static;\n  float: none;\n  box-shadow: none;\n  animation-name: none;\n  padding: 0;\n  background-color: transparent;\n  border-radius: 0;\n}\n\n.dropdown-menu-icon-left-position(@left) {\n  &.rs-dropdown-item-with-icon > .rs-dropdown-item-content {\n    > .rs-dropdown-item-menu-icon {\n      left: @left;\n    }\n\n    > .rs-dropdown-menu-toggle {\n      padding-left: (@left + 20px);\n    }\n  }\n}\n","@import '../../styles/common';\n\n//\n// Slider\n// --------------------------------------------------\n.rs-slider {\n  position: relative;\n\n  .rs-tooltip {\n    display: none;\n\n    &.rs-tooltip-placement-top .rs-tooltip::after {\n      margin: auto;\n      left: 0;\n      right: 0;\n    }\n  }\n\n  &-disabled {\n    opacity: 0.3;\n    cursor: not-allowed;\n\n    .rs-slider-bar,\n    .rs-slider-handle::before {\n      cursor: not-allowed;\n    }\n  }\n\n  &-with-mark:not(&-vertical) {\n    //White space for mark\n    margin-bottom: (@line-height-computed + @slider-mark-margin-top);\n  }\n}\n\n// Bar\n.rs-slider-bar {\n  height: @slider-bar-side-length;\n  border-radius: (@slider-bar-side-length / 2);\n  background-color: var(--rs-slider-bar);\n  transition: background-color ease-in-out 0.3s;\n  cursor: pointer;\n\n  .rs-slider:hover & {\n    background-color: var(--rs-slider-hover-bar);\n  }\n\n  .rs-slider-vertical & {\n    height: 100%;\n    width: @slider-bar-side-length;\n  }\n}\n\n//\n.rs-slider-handle {\n  position: absolute;\n  top: -50%;\n\n  &::before {\n    content: '';\n    position: absolute;\n    width: @slider-handle-diameter;\n    height: @slider-handle-diameter;\n    border-radius: 50%;\n    border: @slider-handle-border-width solid var(--rs-slider-thumb-border);\n    background-color: var(--rs-slider-thumb-bg);\n    margin-left: -(@slider-handle-diameter / 2);\n    cursor: pointer;\n    /* stylelint-disable */ //Formatted by prettier\n    transition: box-shadow @slider-handle-transition, background-color @slider-handle-transition,\n      transform @slider-handle-transition;\n    /* stylelint-enable */\n  }\n\n  &:hover::before,\n  &:focus::before {\n    box-shadow: var(--rs-slider-thumb-hover-shadow);\n  }\n\n  &:active,\n  &.active {\n    &::before {\n      transform: scale(1.2);\n    }\n  }\n\n  .rs-slider-vertical & {\n    top: unset;\n\n    &::before {\n      left: ((@slider-handle-diameter - @slider-bar-side-length)/2);\n      margin-top: (-@slider-handle-diameter / 2);\n    }\n  }\n}\n\n.rs-slider-handle:hover .rs-tooltip,\n.rs-slider-handle.active .rs-tooltip {\n  display: block;\n  opacity: 1;\n  top: -30px;\n\n  .rs-slider-vertical & {\n    top: -33px;\n    // Make sure tooltip align center.\n    margin-left: 3px;\n  }\n}\n\n// Mark\n.rs-slider-mark {\n  position: absolute;\n  top: (@slider-bar-side-length + @slider-mark-margin-top);\n  left: -2px;\n  white-space: nowrap;\n\n  &-content {\n    margin-left: -50%;\n  }\n}\n\n.rs-slider-mark-last {\n  left: auto;\n  right: -2px;\n\n  .rs-slider-mark-content {\n    margin-left: 50%;\n  }\n}\n\n// Graduator\n.rs-slider-graduator {\n  width: 100%;\n\n  ol,\n  li {\n    list-style: none;\n  }\n\n  > ol {\n    display: flex;\n    padding-left: 0;\n    width: 100%;\n\n    > li {\n      flex: 1 1 1%;\n      position: relative;\n\n      &:last-child::after,\n      &::before {\n        content: '';\n        display: block;\n        position: absolute;\n        width: @slider-calibration-diameter;\n        height: @slider-calibration-diameter;\n        border-radius: 50%;\n        background-color: var(--rs-slider-thumb-bg);\n        box-sizing: border-box;\n        border: @slider-calibration-border-width solid var(--rs-slider-bar);\n        margin-left: -4px;\n        top: -1px;\n\n        // Vertical styles\n        .rs-slider-vertical & {\n          top: unset;\n          bottom: -4px;\n          margin-left: -1px;\n        }\n      }\n\n      &:last-child::after {\n        right: -4px;\n\n        // Vertical styles\n        .rs-slider-vertical & {\n          left: 0;\n          bottom: unset;\n          top: -4px;\n        }\n      }\n\n      &.rs-slider-pass::before {\n        border-color: var(--rs-slider-progress);\n      }\n\n      &.rs-slider-active::before {\n        visibility: hidden;\n      }\n    }\n  }\n\n  // Vertical styles\n  .rs-slider-vertical & {\n    display: block;\n    height: 100%;\n\n    > ol {\n      width: @slider-bar-side-length;\n      display: flex;\n      flex-direction: column-reverse;\n      height: 100%;\n      padding: 0;\n\n      & > li {\n        display: block;\n        padding: 0;\n      }\n    }\n  }\n}\n\n// Progress bar\n.rs-slider-progress-bar {\n  position: absolute;\n  height: @slider-bar-side-length;\n  border-radius: (@slider-bar-side-length / 2) 0 0 (@slider-bar-side-length / 2);\n  background-color: var(--rs-slider-progress);\n\n  .rs-slider-vertical & {\n    width: @slider-bar-side-length;\n    border-radius: 0 0 (@slider-bar-side-length / 2) (@slider-bar-side-length / 2);\n  }\n}\n\n// Vertical\n.rs-slider-vertical {\n  height: 100%;\n\n  // Reset all setting\n  .rs-slider-mark {\n    top: unset;\n    bottom: -8px;\n    left: (@slider-bar-side-length + @slider-mark-margin-top);\n\n    &-content {\n      margin-left: auto;\n    }\n  }\n\n  .rs-slider-mark-last {\n    bottom: unset;\n    top: -8px;\n  }\n}\n","@import '../../styles/common';\n\n//\n// Steps\n// --------------------------------------------------\n.rs-steps {\n  min-height: @steps-icon-diameter;\n}\n\n// Item\n// --------------------------\n.rs-steps-item {\n  padding-left: @steps-icon-space-width;\n  position: relative;\n  overflow: hidden;\n}\n\n// Item icon\n.rs-steps-item-icon-wrapper {\n  width: @steps-icon-diameter;\n  height: @steps-icon-diameter;\n  text-align: center;\n  font-size: @steps-icon-size;\n  line-height: @steps-icon-line-height;\n  color: var(--rs-text-secondary);\n  position: absolute;\n  top: 0;\n  left: 0;\n  display: flex;\n  align-items: center;\n  border: 1px solid var(--rs-steps-border);\n  border-radius: 50%;\n\n  // Finish status show check mark.\n  .rs-steps-item-status-finish & {\n    border-color: var(--rs-steps-state-finish);\n    color: var(--rs-steps-state-finish);\n  }\n\n  // Error status show X mark.\n  .rs-steps-item-status-error & {\n    border-color: var(--rs-steps-state-error);\n    color: var(--rs-steps-state-error);\n  }\n\n  .rs-steps-item-status-process & {\n    border-color: var(--rs-steps-state-process);\n    background-color: var(--rs-steps-state-process);\n    color: var(--rs-text-inverse);\n\n    .rs-icon {\n      color: var(--rs-steps-icon-state-process);\n    }\n  }\n\n  &.rs-steps-item-custom-icon {\n    border: none;\n    background: none;\n  }\n\n  // Icon content\n  > .rs-steps-item-icon {\n    width: 100%;\n    display: block;\n    text-align: center;\n    position: relative;\n\n    .rs-icon {\n      font-size: 14px;\n      vertical-align: middle;\n    }\n  }\n}\n\n// Item content\n// --------------------------\n.rs-steps-item-content {\n  color: var(--rs-text-secondary);\n  display: inline-block;\n  // Change static to relative, Make `z-index` more than Tail line\n  position: relative;\n  width: 100%;\n\n  .rs-steps-item-status-process & {\n    color: var(--rs-text-primary);\n  }\n\n  .rs-steps-item-status-error & {\n    color: var(--rs-text-error);\n  }\n}\n\n// Item title\n.rs-steps-item-title {\n  position: relative;\n  font-size: @steps-title-font-size;\n  line-height: @steps-title-line-height;\n  min-height: @steps-title-font-size;\n  padding-right: @steps-icon-spacing;\n  display: inline-block;\n\n  &:empty {\n    padding-right: 0;\n  }\n}\n\n// Item description\n.rs-steps-item-description {\n  margin-top: @steps-description-margin-top;\n}\n\n.rs-steps-item-tail,\n.rs-steps-item-title::after {\n  position: absolute;\n  border-color: var(--rs-steps-border);\n\n  .rs-steps-item-status-process & {\n    border-color: var(--rs-steps-border);\n  }\n\n  .rs-steps-item-status-finish & {\n    border-color: var(--rs-steps-state-finish);\n  }\n\n  .rs-steps-next-error & {\n    border-color: var(--rs-steps-border-state-error);\n  }\n}\n\n// Horizontal\n.rs-steps-horizontal {\n  display: flex;\n  justify-content: space-between;\n\n  .rs-steps-item {\n    flex-grow: 1;\n    flex-shrink: 1;\n    min-height: @steps-icon-diameter;\n\n    // The last node is self-adaptive width.\n    &:last-child {\n      flex-grow: 0;\n      flex-shrink: 0;\n\n      // The last item hidden tail line\n      .rs-steps-item-title::after {\n        display: none;\n      }\n    }\n\n    // There is a 10px spacing between each item.\n    &:not(:first-child) {\n      padding-left: (@steps-icon-space-width + @steps-item-spacing);\n\n      .rs-steps-item-icon-wrapper {\n        left: @steps-item-spacing;\n      }\n    }\n  }\n\n  .rs-steps-item-content {\n    height: (@steps-title-font-size * @steps-title-line-height);\n  }\n\n  .rs-steps-item-title {\n    &::after {\n      content: '';\n      top: (@steps-icon-diameter / 2);\n      left: 100%;\n      width: 9999px;\n      border-top-width: 1px;\n      border-top-style: solid;\n    }\n  }\n}\n\n// Verticla\n.rs-steps-vertical {\n  .rs-steps-item {\n    padding-bottom: 20px;\n\n    // There is a 10px spacing between each item.\n    &:not(:first-child) {\n      margin-top: @steps-item-spacing;\n    }\n\n    &:last-child .rs-steps-item-tail {\n      display: none;\n    }\n  }\n\n  .rs-steps-item-tail {\n    top: @steps-icon-space-width;\n    bottom: 0;\n    left: (@steps-icon-diameter / 2);\n    border-left-width: 1px;\n    border-left-style: solid;\n  }\n}\n\n// Small\n// Resize: Diameterfont-size and so on.\n.rs-steps-small {\n  min-height: @small-steps-icon-diameter;\n\n  &.rs-steps-horizontal .rs-steps-item {\n    min-height: @small-steps-icon-diameter;\n  }\n\n  &.rs-steps-horizontal .rs-steps-item:not(:first-child) {\n    padding-left: (@small-steps-icon-space-width + @steps-icon-spacing);\n  }\n\n  &.rs-steps-vertical .rs-steps-item-tail {\n    left: (@small-steps-icon-diameter / 2);\n  }\n\n  &.rs-steps-horizontal .rs-steps-item-title::after {\n    top: 12px;\n  }\n\n  .rs-steps-item {\n    padding-left: @small-steps-icon-space-width;\n  }\n\n  .rs-steps-item-icon-wrapper {\n    width: @small-steps-icon-diameter;\n   